@import url('https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,300;0,500;0,800;1,600&display=swap');
    
.header-font{
    font-family: 'Work Sans', sans-serif;
    letter-spacing:0.2em;
    font-size: 30px;
    font-weight: 300;
}

.title {
    font-family: 'Work Sans', sans-serif;
    letter-spacing:0.2em;
    font-size: 20px;
    font-weight: 500;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #121212; 
}

.App-logo {
  height: 40vmin;
}

.App-header {
  color: white;
}

.App-link {
  color: #09d3ac;
}

.top{ 
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
} 

.top-padding{
    padding-top: 5%;
}

.bottom-padding{
    padding-top: 15%;
}

.white-text{
    color: ghostwhite;
}

.header {
    text-align: center;
}

.test {
    border-style: solid;
    border-color: skyblue;
}

.test-alt {
   border-style: solid;
   border-color: orangered;
}

html {
  scroll-behavior: smooth;
}

